            bx.push(x1);
            by.push(y1);
            bx.push(x1);
            by.push(y2);
            bx.push(x0);
            by.push(y1);
            bx.push(x0);
            by.push(y2);
            bx.push(x2);
            by.push(y0);
            bx.push(x2);
            by.push(y1);
