                path.py = Vec.append(path.py, qy1);
                path.ctrl = Vec.append(path.ctrl, true);
                path.px = Vec.append(path.px, x[n]);
                path.py = Vec.append(path.py, y[n]);
                path.ctrl = Vec.append(path.ctrl, false);
                path.px = Vec.append(path.px, qx2);
                path.py = Vec.append(path.py, qy2);
                path.ctrl = Vec.append(path.ctrl, true);
